    font-family: Lusitana, serif
}
table.tableizer-table {
		width: 100%;
		font-size: 13px;
		border: 1px #ffffff; 
		font-family: Poppins, serif;
		table-layout: fixed;
	} 
	.tableizer-table td {
		padding: 8px;
		margin: 8px;
		border: 1px solid #ffffff;
		border-right: none;
		border-left: none;
	}
	.tableizer-table th {
		background-color: #484848; 
		border: 1px solid #484848;
		border-bottom: 1px solid #ffffff;
		font-weight: bold;
		font-family: Lusitana;
		text-align: start;
		padding: 4px 8px;
		margin: 8px;
	}
